<template>
  <v-navigation-drawer
    :value="showDrawer"
    width="200"
    app
    clipped
    @input="syncDrawer($event)">
    <v-subheader class="mt-4">Categories</v-subheader>

    <v-list dense>
      <v-list-item
        v-for="category in getCategories"
        :key="category.text"
        link
        @click="setCategory(category.text), $router.push('/')"
      >
        <v-list-item-title v-text="category.text"/>
      </v-list-item>
    </v-list>

    <v-divider
      class="mx-4"
    ></v-divider>

    <v-list dense>
      <v-list-item
        v-for="page in getDrawerPages"
        :key="page.text"
        link
        @click="$router.push(page.route)"
      >
        <v-list-item-action>
          <v-icon>{{ page.icon }}</v-icon>
        </v-list-item-action>
        <v-list-item-content>
          <v-list-item-title>
            {{ page.text }}
          </v-list-item-title>
        </v-list-item-content>
      </v-list-item>
    </v-list>
  </v-navigation-drawer>
</template>

<script>
  export default {
    name: "Drawer",
    computed:{
      showDrawer () {
        return this.$store.getters.getDrawerState
      },
      getCategories () {
        return this.$store.getters.getCategories
      },
      getDrawerPages () {
        return this.$store.getters.getDrawerPages
      }
    },
    methods: {
      setCategory(category) {
        this.$store.commit('setActiveCategory', category)
      },
      syncDrawer(payload) {
        if (payload == false){
          this.$store.commit('setDrawerState', false);
        } else {
          this.$store.commit('setDrawerState', true);
        }
      }
    },
  }
</script>

