import Vue from 'vue'
import Router from 'vue-router'
import About from "@/components/About";
import Contact from "@/components/Contact";
import Footer from "@/components/Footer";
import MainPage from "@/components/MainPage";
import Drawer from "@/components/Drawer";
import Header from "@/components/Header";
import Partners from "../components/Partners";
import Popular from "../components/Popular";
import Latest from "../components/Latest";

Vue.use(Router);

export default new Router({
  linkExactActiveClass: 'active',
  routes: [
    {
      path: '/',
      name: 'home',
      components: { default: MainPage, footer: Footer, header: Header, drawer: Drawer},
      props: {
        footer: { backgroundColor: 'white' }
      }
    },
    {
      path: '/about',
      name: 'about',
      components: { default: About, footer: Footer, header: Header, drawer: Drawer},
      props: {
        footer: { backgroundColor: 'white' }
      }
    },
    {
      path: '/contact',
      name: 'contact',
      components: { default: Contact, footer: Footer, header: Header, drawer: Drawer},
      props: {
        footer: { backgroundColor: 'black' }
      }
    },
    {
      path: '/partners',
      name: 'partners',
      components: { default: Partners, footer: Footer, header: Header, drawer: Drawer},
      props: {
        footer: { backgroundColor: 'black' }
      }
    },
    {
      path: '/popular',
      name: 'popular',
      components: { default: Popular, footer: Footer, header: Header, drawer: Drawer},
      props: {
        footer: { backgroundColor: 'black' }
      }
    },
    {
      path: '/latest',
      name: 'latest',
      components: { default: Latest, footer: Footer, header: Header, drawer: Drawer},
      props: {
        footer: { backgroundColor: 'black' }
      }
    },
  ],
  scrollBehavior: to => {
    if (to.hash) {
      return { selector: to.hash };
    } else {
      return { x: 0, y: 0 };
    }
  }
});